@import url(https://fonts.googleapis.com/css?family=Karla:400,700);
@primary-color :$primary-color;
@link-color : $link-color;
body {
  margin: 0;
  padding: 0;
  font-family: "Karla", sans-serif, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black; }

.App {
  display: flex;
  flex-direction: column;
  height: 100vh; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  background-color: white; }

.ant-carousel .slick-slide {
  text-align: center;
  width: 80%;
  height: 70vh;
  line-height: 160px;
  display: flex;
  overflow: hidden; }

.ant-carousel .slick-slide h3 {
  color: #000; }

.ant-carousel .slick-dots li button {
  background-color: #000; }

.ant-carousel .slick-dots li.slick-active button {
  background-color: #000; }

@primary-color :$primary-color;
@link-color : $link-color;
main {
  display: flex;
  justify-content: center;
  flex: 1 0; }

.grower {
  flex-grow: 5;
  min-height: 5px; }

.fix-top {
  flex: 0 0 60px; }

.fix-bottom {
  flex: 0 0 60px; }

footer {
  background-color: white; }

@primary-color :$primary-color;
@link-color : $link-color;
.nav-style {
  color: black;
  background-color: white;
  z-index: 3;
  margin: 0;
  padding: 0;
  height: 60px;
  display: flex;
  align-content: center;
  justify-content: center;
  width: 100vw;
  border-radius: 0px; }
  .nav-style ul {
    display: flex; }
  .nav-style a {
    display: inline;
    color: black;
    text-decoration: none;
    align-self: center;
    justify-self: center; }
    .nav-style a :hover {
      color: pink; }

.nav-link {
  align-self: center;
  justify-self: center;
  font-family: "Karla", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  padding: 0 10px; }

.nav-width {
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-direction: row;
  justify-content: space-between; }

#top-nav {
  position: fixed;
  top: 0; }

#bottom-nav {
  position: fixed;
  bottom: 0; }

@primary-color :$primary-color;
@link-color : $link-color;
section {
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center; }

.landing-wrapper {
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-self: center;
  padding: 20px 0; }

.landing-text {
  font-family: "Karla", sans-serif;
  text-align: center;
  font-size: 3.5rem;
  color: black; }

.landing-link {
  text-decoration: none;
  color: white;
  width: 100%;
  align-self: center;
  text-align: center;
  justify-self: center; }

.landing-random {
  display: inline; }

.action {
  font-weight: 400; }

.object {
  font-weight: 700; }

.lang-button {
  align-self: center;
  background-color: black;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  text-align: center;
  font-family: "Karla", sans-serif;
  font-size: 10px;
  font-weight: 700; }

.landing-area {
  max-height: 600px;
  height: 600px;
  width: 600px;
