.project-detail {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center; }

.projects-title {
  font-size: 15px;
  text-align: center;
  text-align: center; }

.project-description {
  text-align: center; }

.project-context {
  text-align: center;
